<template>
  <!--  -->
  <div class="general-course">
    <div class="all-course">
      <h3></h3>
      <div class="course-package-top">
        <el-form ref="form" label-width="96px">
          <div class="inps">
            <el-row>
              <el-form-item label="">
                <el-input size="mini" v-model="form.teacher_name"></el-input>
              </el-form-item>
              <el-form-item class="select-time" label="">
                <el-date-picker
                  v-model="form.class_at"
                  type="datetime"
                  size="mini"
                  placeholder="">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="form.course_schedule_state" placeholder="" size="mini">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value="0"></el-option>
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="2"></el-option>
                  <el-option label="" value="3"></el-option>
                  <el-option label="" value="4"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="form.courseware_state" placeholder="" size="mini">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value="0"></el-option>
                  <el-option label="" value="1"></el-option>
                </el-select>
              </el-form-item>
            </el-row>
          </div>
          <el-button type="primary" size="mini"></el-button>
        </el-form>
      </div>
    </div>
    <div class="table-list">
      <el-table :data="tableData" style="width: 100%;margin-top:20px;">
        <el-table-column fixed prop="id" label="" style="width: 10%;">
        </el-table-column>
        <el-table-column prop="course_name" label="" style="width: 15%;">
        </el-table-column>
        <el-table-column prop="grade" label="" style="width: 10%;">
        </el-table-column>
        <el-table-column prop="teacher_name" label="" style="width: 10%;">
        </el-table-column>
        <el-table-column prop="student_name" label="" style="width: 10%;">
        </el-table-column>
        <el-table-column prop="start" label="" width="200">
        </el-table-column>
        <el-table-column prop="courseware_num" label="" style="width: 10%;">
        </el-table-column>
        <el-table-column prop="course_schedule_state" label="" style="width: 10%;">
          <template slot-scope="scope">
            {{$COURSE_SCHEDULE_STATE[scope.row.course_schedule_state]}}
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="" style="width: 15%;">
          <template slot-scope="scope">
            <el-button size="mini" v-if="!scope.row.course_schedule_id" @click="toSetSchedule(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-row class="block">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="form.page_no"
          :page-size="10"
          layout="total, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </el-row>
    </div>

  </div>
</template>
<script>
  import {
    mangerThacherCommon
  } from '@/api/course'
  export default {
    data() {
      return {
        tableData: [],
        total: 0,
        form: {
          page_no: 1,
          class_at: '',
          course_schedule_state: '',
          courseware_state: '',
          teacher_name: ''
        }
      }
    },
    created() {
      this.query();
    },
    methods: {
      handleCurrentChange(val) {
        this.form.page_no = val;
        this.query();
      },
      query() {
        const f = this.$deleteEmptyProps(this.form);
        return mangerThacherCommon({
          ...f,
          page_limit: 10
        }).then(resp => {
          this.total = resp.data.num_results;
          this.tableData = resp.data.objects;
        })
      },
      toSetSchedule(id) {
        this.$router.push({
          path: '/course/subscribeCourses',
          query: {id}
        })
      }
    }
  }
</script>

<style scoped>
  .inps .el-form-item {
    width: 20%;
    float: left;
  }

  .el-input {
    width: 95%;
  }

  .inps .select-time {
    width: 35%;
  }

  .operation button {
    margin-right: 40px;
  }

  h3 {
    padding: 20px 0;
    margin-left: 10px;
  }

  .course-package-top button {
    float: left;
    margin: 10px;
  }

  .course-package-top {
    overflow: hidden;
  }

  .operation {
    margin-left: 20px;
    margin-left: 20px;
  }

  .block {
    text-align: right;
  }
</style>



