/**
 *  
 * @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="pageSizes"
 *  form.curPage
 * query
 * 
 * 
 */
export default {
  data() {
    return {
      pageSizes: [10, 20, 30, 40]
    };
  },
  methods: {
    query() {
    },
    handleCurrentChange(e) {
      this.form.page = e;
      this.query();
    }
  }
}


