			<el-form>
				<el-form-item label="">
					<el-input type="textarea" :rows="5" :disabled="canEdit" v-model="failReason"></el-input>
				</el-form-item>
			</el-form>
		</div>
		<div class="btn-box">
			<el-button type="primary" size="mini" @click="submit"></el-button>
			<el-button size="mini"></el-button>
		</div>
	</div>
</template>
<script>
import {
	interviewPutByinterviewid,
	interviewGetByinterviewid
} from '@/api/interview';
import {
	teacherPutByTeacherid,
	teacherGetBareByTeacherid
} from '@/api/teacher';
export default {
  data() {
    return {
			canEdit: true,
			teacher_id: '',
			interview_id: '',
			// 1 2
			state: 0,
			form: {
				//  1  0 
				onlineExperience: 1,
				// 
				roomVisit: {
					// 1  0 
					value: 1,
					// 
					remark: ''
				},
				//  1  0 
				gender: 1,
				//  1  2  3  4  5  6 
				skin: 1,
				// 
				teacherExperience: {
					// 1  0 
					value: 1,
					// 
					remark: ''
				},
				// 
				educationHistory: {
					// 1  2  3 100
					value: 1,
					// 
					remark: ''
				},
				// 
				workShool: {
					// 1  2  3 100
					value: 1,
					// 
					remark: ''
				},
				// demo 1  2 
				demoCourseWay: 1,
				// 
				teacheFeature: '',
				// PPT
				pptTheme: '',
				// PPT
				pptDesign: '',
				// PPT
				pptContent: '',
				//  1 20-25 2 25-30 3 30-35 4 35-40 5 40
				suggestSalary: 1,
				// 
				emailNumRemark: '',
				// 
				selfCharmRemark: '',

				// 
				//   5 6-15 15
				localWorkAge: 5,
				//  1  2  3 
				highestDegree: 5,
				// 
				teacheAndManage: {
					// 
					teache: 0,
					// 
					manage: 0,
					// 
					consult: 0
				},
				//  
				interviewStatus: {
					// 
					light: 0,
					// 
					background: 0
				},
				// 
				onlineRoomStatus: {
					// 
					enter: 0,
					// 
					upload: 0,
					// 
					other: 0
				},
				// 
				onlineTeache: {
					// 
					experience: 0,
					// 
					isLively: 0,
					// ppt
					ppt: 0,
					// 
					voice: 0
				},
				// 
				coursewareStyle: {
					// 
					isLively: 0,
					// 
					design: 0,
					// 
					action: 0,
					// 
					property: 0
				},
				// 
				coursewareContent: {
					// 
					goal: 0,
					// 
					word: 0,
					// 
					interaction: 0,
					// 
					rich: 0,
					// 
					clear: 0
				},
				// /
				teacheStatus: {
					// 
					interaction: 0,
					// 
					affect: 0,
					// 
					flexible: 0,
					// 
					speed: 0
				},
				// 
				customMade: {
					// 
					hasCustomMade: 0,
					// 
					hasChiness: 0
				},
				// 
				attainment: {
					// 
					onTime: 0,
					// 
					loveTeache: 0
				},
				// 
				summary: ''
			},
			//  3  5 
			result: 3,
			failReason: ''
    };
	},
	computed: {
		sumScore() {
			return this.form.localWorkAge + 
						 this.form.highestDegree +
						 this.teacheAndManageSum + 
						 this.interviewStatusSum +
						 this.onlineRoomStatusSum +
						 this.onlineTeacheSum +
						 this.coursewareStyleSum +
						 this.coursewareContentSum +
						 this.teacheStatusSum +
						 this.customMadeSum +
						 this.attainmentSum;
			
		},
		teacheAndManageSum() {
			return this.form.teacheAndManage.teache + 
						 this.form.teacheAndManage.manage +
						 this.form.teacheAndManage.consult;
		},
		interviewStatusSum() {
			return this.form.interviewStatus.light + 
						 this.form.interviewStatus.background; 
		},
		onlineRoomStatusSum() {
			return this.form.onlineRoomStatus.enter + 
						 this.form.onlineRoomStatus.upload + 
						 this.form.onlineRoomStatus.other; 
		},
		onlineTeacheSum() {
			return this.form.onlineTeache.experience + 
						 this.form.onlineTeache.isLively + 
						 this.form.onlineTeache.ppt + 
						 this.form.onlineTeache.voice; 
		},
		coursewareStyleSum() {
			return this.form.coursewareStyle.isLively + 
						 this.form.coursewareStyle.design + 
						 this.form.coursewareStyle.action + 
						 this.form.coursewareStyle.property; 
		},
		coursewareContentSum() {
			return this.form.coursewareContent.goal + 
						 this.form.coursewareContent.word + 
						 this.form.coursewareContent.interaction + 
						 this.form.coursewareContent.rich + 
						 this.form.coursewareContent.clear; 
		},
		teacheStatusSum() {
			return this.form.teacheStatus.interaction + 
						 this.form.teacheStatus.affect + 
						 this.form.teacheStatus.flexible + 
						 this.form.teacheStatus.speed; 
		},
		customMadeSum() {
			return this.form.customMade.hasCustomMade + 
						 this.form.customMade.hasChiness; 
		},
		attainmentSum() {
			return this.form.attainment.onTime + 
						 this.form.attainment.loveTeache; 
		}
	},
	created() {
		this.interview_id = this.$route.query.id;
		this.getInterview(this.$route.query.id);
	},
	methods: {
		getInterview(id) {
			interviewGetByinterviewid(id).then(resp => {
				if(resp.data.result) {
					this.canEdit = false;
					let result = {};
					try {
						result = JSON.parse(resp.data.result);
					} catch(e) {
					}
					this.form = this.$_object.assign(this.form, result);
				} else {
					this.canEdit = false;
				}
				this.result = resp.data.state;
				this.teacher_id = resp.data.teacher_id;
				this.getTeacher(resp.data.teacher_id, resp.data.result);
			})
		},
		getTeacher(id, reason) {
			teacherGetBareByTeacherid(id).then(resp => {
				if(resp.data.state === 12) {
					this.failReason = reason;
					// 
					return this.state = 2;
				}
				return this.state = 1;
			});
		},
		submit() {
			if(this.state === 1) {
				// 
				if(this.result === 3) {
					// 
					teacherPutByTeacherid(this.teacher_id, {
						state: 'WAITE_FOR_CONTRACT'
					}).then(resp => {
						return interviewPutByinterviewid(this.interview_id, {
							state: 9,
							result: JSON.stringify(this.form)
						})
					}).then(_=>{
						this.$message.success('');
						this.back();
					})
				} else {
					// 
					interviewPutByinterviewid(this.interview_id, {
						state: this.result,
						result: JSON.stringify(this.form)
					}).then(resp => {
						this.$message.success('');
						this.back();
					});
				}
			} else {
				// 
				if(this.failReason) {
					interviewPutByinterviewid(this.interview_id, {
						state: 10,
						result: this.failReason
					}).then(_=>{
						this.$message.success('');
						this.back();
					})
				} else {
					this.$message.error('');
				}
			}
		},
		back(){
			this.$router.back();
		}
	}
};
</script>

<style scoped>
.reinterview {
  padding: 10px;
}
.main-content {
  width: 100%;
  min-height: 200px;
  border: 1px solid #e1e1e1;
}
.interview-form {
  width: 100%;
  min-height: 200px;
  border: 1px solid #e1e1e1;
  margin-top: 80px;
  overflow: hidden;
}
.btn-box {
  width: 250px;
  margin: 0 auto;
  padding: 15px;
}
.tits {
  height: 50px;
  text-align: center;
  line-height: 50px;
  font-size: 13px;
  border-bottom: 1px solid #e1e1e1;
}
.jingyan {
  border-bottom: 1px solid #e1e1e1;
  height: 50px;
  line-height: 50px;
  overflow: hidden;
}
.line-jy {
  width: 20%;
  border-right: 1px solid #e1e1e1;
  float: left;
  text-align: center;
}
.sele {
  float: left;
	padding-left: 10px;
  width: 79%;
}
.seles {
  float: left;
  width: 20%;
	padding-left: 10px;
  border-right: 1px solid #e1e1e1;
}
.jsqk {
  height: 100px;
}
.jsqk .line-jy {
  height: 100px;
  line-height: 100px;
}
.beizhu {
  width: 59%;
  float: left;
}
.beizhu span {
  float: left;
  text-indent: 20px;
}
.el-textarea {
  display: inline-block;
  padding: 14px;
  width: 80%;
  height: 60px;
}
.selesa {
  line-height: 33px;
  float: left;
	padding-left: 10px;
  width: 20%;
  border-right: 1px solid #e1e1e1;
}
.jingyan .el-textarea {
  padding: 10px;
}
.grade {
  float: right;
  margin-right: 12px;
}
.line1 {
  overflow: hidden;
  border-bottom: 1px solid #e1e1e1;
  height: 50px;
  line-height: 50px;
}
.line1 div {
  float: left;
  text-align: center;
}
.num,
.manf {
  width: 10%;
  border-right: 1px solid #e1e1e1;
}
.cons {
  width: 25%;
  border-right: 1px solid #e1e1e1;
}
.detail {
  width: 54%;
}
.line2 {
  height: 150px;
  border-bottom: 1px solid #e1e1e1;
  float: left;
  width: 100%;
}
.line2 .content {
  float: left;
  text-align: center;
  border-right: 1px solid #e1e1e1;
  height: 150px;
  width: 14.9%;
}
.nums,
.base {
  width: 10%;
  line-height: 150px;
  float: left;
  border-right: 1px solid #e1e1e1;
  text-align: center;
}
.jiaoling,
.xueli {
  border-bottom: 1px solid #e1e1e1;
  height: 50px;
  line-height: 50px;
}
.grades {
  width: 10%;
  line-height: 50px;
  float: left;
  border-right: 1px solid #e1e1e1;
  text-align: center;
}
.grades1,
.grades2 {
  border-bottom: 1px solid #e1e1e1;
}
.jygl {
  line-height: 50px;
}
.five {
  width: 15%;
  float: left;
  height: 150px;
  border-right: 1px solid #e1e1e1;
}
.conts {
  height: 24px;
  border-bottom: 1px solid #e1e1e1;
  text-align: center;
  line-height: 24px;
}
.cont {
  border: none;
  height: 25px;
}
.gradess {
  width: 9.5%;
  border: none;
}
.xg {
  width: 45.25%;
  height: 150px;
  float: left;
  overflow: hidden;
}
.xg-top {
  border-bottom: 1px solid #e1e1e1;
  height: 50px;
  overflow: hidden;
}
.dg {
  float: left;
  border-right: 1px solid #e1e1e1;
  height: 50px;
  text-align: center;
  width: 49.8%;
}
.word,
.shuzi {
  border-bottom: 1px solid #e1e1e1;
  height: 25px;
  line-height: 25px;
}
.gradss {
  width: 9.5%;
  border-right: none;
}
.djs {
  width: 33.13%;
  float: left;
  border-right: 1px solid #e1e1e1;
  text-align: center;
}
.xsskjl {
  width: 24.81%;
  float: left;
  border-right: 1px solid #e1e1e1;
  text-align: center;
}
.xsskjls {
  width: 19.8%;
  float: left;
  border-right: 1px solid #e1e1e1;
  text-align: center;
}
.line4 {
  height: 50px;
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #e1e1e1;
  overflow: hidden;
}
.line-num,
.line-name,
.line-gtade {
  line-height: 50px;
  width: 10%;
  border-right: 1px solid #e1e1e1;
  float: left;
}
.line-cont {
  width: 14.9%;
