  outline: none;
}
.xgls {
  float: left;
	padding-left: 10px;
  line-height: 50px;
}
.xgls input {
  margin-left: 80px;
}
.reason-content {
	overflow: hidden;
  min-height: 200px;
	margin-bottom: 20px;
}
</style>


