<template>
  <!--  -->
  <div class="auditions">
    <div class="operation">
      <router-link to="/course/studentTrial">
        <el-button size="mini" type="primary"></el-button>
      </router-link>
      <router-link to="/course/dateTeacher">
        <el-button size="mini" type="primary"></el-button>
      </router-link>
    </div>
    <div class="all-course">
      <div class="course-package-top">
        <el-form :inline="true" ref="form" :model="form" label-width="82px">
          <el-row>
            <el-form-item label="">
              <el-input size="mini" v-model="form.teacher_name" placeholder="" type="text" class="placehold" />
            </el-form-item>
            <el-form-item label="">
              <el-date-picker size="mini" v-model="form.class_at" placeholder=""></el-date-picker>
            </el-form-item>
            <el-form-item label="">
              <el-select size="mini" v-model="form.course_schedule_state" name="" id="">
                <el-option value=""></el-option>
                <el-option value="1"></el-option>
                <el-option value="2"></el-option>
                <el-option value="3"></el-option>
                <el-option value="4"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select size="mini" v-model="form.courseware_state" name="" id="">
                <el-option value=""></el-option>
                <el-option value="1"></el-option>
                <el-option value="0"></el-option>
              </el-select>
            </el-form-item>
          </el-row>
          <el-row>
            <el-button size="mini" type="primary"></el-button>
          </el-row>
        </el-form>
      </div>
    </div>
    <div class="table-list">

      <el-table :data="tableData" style="width: 100%;margin-top:20px;">
        <el-table-column fixed prop="zhname" label="" style="width: 15%;">
        </el-table-column>
        <el-table-column prop="enname" label="" style="width: 15%;">
        </el-table-column>
        <el-table-column prop="zip" label="" style="width: 10%;">
        </el-table-column>
        <el-table-column prop="province" label="" style="width: 10%;">
        </el-table-column>
        <el-table-column prop="address" label="" style="width: 10%;">
        </el-table-column>
        <el-table-column prop="date" label="" style="width: 10%;">
        </el-table-column>
        <el-table-column prop="zip" label="" style="width: 10%;">
        </el-table-column>
        <el-table-column prop="city" label="" style="width: 15%;">
        </el-table-column>
        <el-table-column fixed="right" label="" style="width: 15%;">
          <template slot-scope="scope">
            <button type="button" class="el-button el-button--default el-button--small">
              <span></span>
            </button>
          </template>
        </el-table-column>
      </el-table>
      <div class="block">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="form.page_no"
          :page-size="10"
          layout="total, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
  import {
    studentTryout
  } from '@/api/course'
  export default {
    data() {
      return {
        tableData: [],
        total: 0,
        form: {
          page_limit: 10,
          page_no: 1,
          teacher_name: '',
          class_at: '',
          course_schedule_state: '',
          courseware_state: ''
        }
      }
    },
    created() {
      this.query();
    },
    methods: {
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.form.page_no = val;
        this.query()
      },
      query() {
        const f = this.$deleteEmptyProps(this.form);
        studentTryout(f).then(resp => {
          this.tableData = resp.data.objects;
          this.total = resp.data.num_results;
        })
      }
    },
  }
</script>

<style scoped>
  .operation {
    margin-bottom: 20px;
  }
  .block {
    margin: 0 auto;
    padding: 20px;
    width: 600px;
  }
</style>



