<template>
<!--  -->
	<div class="lecture">
		<div class="operation">
		</div>
		<div class="all-course">
			<h3></h3>
			<div class="course-package-top">
				<el-form ref="form" label-width="96px">
				<div class="inps">
					<el-row>
						<el-form-item label="">
							<el-input size="mini" v-model="form.teacher_name"></el-input>
						</el-form-item>
						<el-form-item class="select-time" label="">
              <el-date-picker
                v-model="form.class_at"
                type="datetime"
                size="mini"
                placeholder="">
              </el-date-picker>
						</el-form-item>
						<el-form-item label="">
							<el-select v-model="form.course_schedule_state" placeholder="" size="mini">
								<el-option label="" value=""></el-option>
								<el-option label="" value="1"></el-option>
								<el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="4"></el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="">
							<el-select v-model="form.courseware_state" placeholder="" size="mini">
								<el-option label="" value=""></el-option>
								<el-option label="" value="0"></el-option>
								<el-option label="" value="1"></el-option>
							</el-select>
						</el-form-item>
					</el-row>
				</div>
				<el-button type="primary" size="mini"></el-button>
				<router-link to="/course/dateClass"><el-button type="primary" size="mini"></el-button></router-link>
        <router-link to="/course/lectureInformation"><el-button type="primary" size="mini"></el-button></router-link>
				</el-form>
			</div>
		</div>
		<div class="table-list">
			<el-table :data="tableData" style="width: 100%;margin-top:20px;">
					<el-table-column fixed prop="id" label="" style="width: 15%;">
					</el-table-column>
					<el-table-column prop="teacher_name" label="" style="width: 15%;">
					</el-table-column>
          <el-table-column prop="course_name" label="" style="width: 15%;">
          </el-table-column>
          <el-table-column prop="student_name" label="" style="width: 10%;">
          </el-table-column>
					<el-table-column prop="grade" label="" style="width: 10%;">
					</el-table-column>
					<el-table-column prop="start" label="" style="width: 10%;">
					</el-table-column>
					<el-table-column prop="courseware_num" label="" style="width: 10%;">
					</el-table-column>
					<el-table-column prop="course_schedule_state" label="" style="width: 15%;">
					</el-table-column>
					<el-table-column fixed="right" label="" style="width: 15%;">
					<template slot-scope="scope">
							<button type="button" class="el-button el-button--default el-button--small">
									<span></span>
							</button>
					</template>
					</el-table-column>
			</el-table>
			<el-row class="block">
					<el-pagination
					@current-change="handleCurrentChange"
					:current-page="form.page_no"
					:page-size="10"
					layout="total, prev, pager, next, jumper"
					:total="total">
					</el-pagination>
			</el-row>
		</div>
	</div>
</template>

<script>
  import {
    mangerThacherTryout
  } from '@/api/course_appointment'
  export default {
    data() {
      return {
        tableData: [],
        total: 0,
        form: {
          page_no: 1,
          course_schedule_state: '',
          courseware_state: '',
          class_at: '',
          teacher_name: ''
        }
      };
    },
    created() {
      this.query();
    },
    methods: {
      handleCurrentChange(val) {
        this.form.page_no = val;
        this.query();
      },
      query() {
        const f = this.$deleteEmptyProps(this.form);
        mangerThacherTryout({
          ...f,
          page_limit: 10,
        }).then(resp => {
          this.tableData = resp.data.objects;
          this.total = resp.data.num_results;
        })
      }
    }
  };
</script>

<style scoped>
.operation button {
  margin-right: 40px;
}
h3 {
  padding: 20px 0;
  margin-left: 10px;
}
.inps .el-form-item {
  width: 20%;
  float: left;
}
.inps .select-time {
  width: 35%;
}
.course-package-top button {
  float: left;
  margin: 10px;
}
.course-package-top {
  overflow: hidden;
}
.operation {
  margin-left: 20px;
  margin-left: 20px;
}
.create-time {
  width: 45%;
  float: left;
  padding: 10px;
}
.create-time div {
  width: 35%;
  margin-right: 20px;
}
.block {
  text-align: right;
}
</style>



