<template>
	<!--  -->
	<div class="contract-mess">
		<h4></h4>
		<div class="teacher-mess">
			<div class="teacher-name">
				<div class="title"></div>
				<div class="cont">Kira Yuan</div>
			</div>
			<div class="teacher-name">
				<div class="title"></div>
				<div class="cont">1-876543210</div>
			</div>
			<div class="teacher-name">
				<div class="title"></div>
				<div class="cont">kira@gmail.com</div>
			</div>
		</div>
		<h4></h4>
		<div class="agreement">
			<div class="price">
				<div class="title"></div>
				<div>USD <input class="inp" type="text" v-model="contract_dollar_price"> /</div>
			</div>
		</div>
		<div class="btn-box">
			<el-button type="primary" size="mini"></el-button>
			<el-button type="primary" size="mini"></el-button>
			<el-button size="mini"></el-button>

		</div>
	</div>
</template>

<script>
import {
	teacherPutByTeacherid
} from '@/api/teacher'
export default {
	data() {
		return {
			id: '',
			form: {
				contract_dollar_price: ''
			}
		}
	},
	created() {
		this.id = this.$route.query.id;
	},
	methods: {
		valid() {
			if(!this.form.contract_dollar_price) {
				this.$message.error('')
				return false;
			}
			if(!this.$PLUS_NUM_REG_EXP.test(this.form.contract_dollar_price)) {
				this.$message.error('')
				return false;
			}
			return true;
		},
		submit() {
			if(this.valid()) {
				teacherPutByTeacherid(id, {
					contract_dollar_price: this.form.contract_dollar_price
				}).then(resp => {
					this.$message.success('');
				});
			}
		}
	}
};
</script>

<style scoped>
h4 {
  margin: 20px;
}
.teacher-mess,
.agreement,
.btn-box {
  margin: 20px;
}
.teacher-name,
.type,
.price {
  width: 100%;
  height: 30px;
  line-height: 30px;
  overflow: hidden;
}
.title {
  float: left;
  width: 100px;
  padding-right: 20px;
}
.cont {
  float: left;
}
.inp {
  width: 50px;
  text-align: center;
}
</style>


