<template>
	<!--  -->
	<div class="teacher-detail">
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
				<img-upload 
					name="file"
					:preUrl="$baseApiUrl"
					:uploadUrl="$baseApiUrl + '/upload'"
					v-model="avatar"
					:maxNum="1"
				></img-upload>
			</el-main>
		</el-container>
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
        <el-row>
          <el-input size="mini" placeholder="" v-model="form.first_name" class="detail-item-width3"></el-input>
        </el-row>
			</el-main>
		</el-container>
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
        <el-row>
          <el-input size="mini" placeholder="" v-model="form.first_name" class="detail-item-width3"></el-input>
        </el-row>
			</el-main>
		</el-container>
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
        <el-row>
					<el-radio-group v-model="form.gender">
						<el-radio :label="1"></el-radio>
						<el-radio :label="2"></el-radio>
					</el-radio-group>
        </el-row>
			</el-main>
		</el-container>
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
        <el-row>
          <el-date-picker
            v-model="form.date"
            size="mini"
            type="date"
						class="detail-item-width3"
            placeholder="">
          </el-date-picker>
        </el-row>
			</el-main>
		</el-container>
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
				<area-select v-model="areaLs" ref="areaSelect" :type="2" item-width="153px"></area-select >
			</el-main>
		</el-container>
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
        <el-row class="item-row">
					<el-input size="mini" v-model="form.street" class="detail-item-width3"></el-input>
        </el-row>
        <el-row class="item-row">
					<el-input size="mini" v-model="form.street" class="detail-item-width3"></el-input>
        </el-row>
			</el-main>
		</el-container>
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
        <el-row>
					<el-select v-model="form.gender" placeholder="" size="mini" class="detail-item-width3">
						<el-option v-for="(item, key) in $GRADE_ENUMS" :key="key" :label="item" :value="key"></el-option>
					</el-select>
        </el-row>
			</el-main>
		</el-container>

		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
        <el-row class="item-row">
					<el-input size="mini" type="textarea" :row="3" v-model="form.street" class="detail-item-width3"></el-input>
        </el-row>
        <el-row class="item-row">
					<el-input size="mini" type="textarea" :row="3" v-model="form.street" class="detail-item-width3"></el-input>
        </el-row>
			</el-main>
		</el-container>
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
        <el-row class="item-row">
					<el-input size="mini" type="textarea" :row="3" v-model="form.street" class="detail-item-width3"></el-input>
        </el-row>
        <el-row class="item-row">
					<el-input size="mini" type="textarea" :row="3" v-model="form.street" class="detail-item-width3"></el-input>
        </el-row>
			</el-main>
		</el-container>
		<el-container class="detail-item">
			<el-aside class="detail-item-aside" width="200px">
				
			</el-aside>
			<el-main class="detail-item-main">
        <el-row>
					<el-radio-group v-model="form.gender">
						<el-radio :label="1"></el-radio>
						<el-radio :label="2"></el-radio>
					</el-radio-group>
        </el-row>
			</el-main>
		</el-container>
	</div>
</template>

<script>
export default {
	data() {
		return {
			avatar: [], //
			areaLs: [],
			form: {
				avatar: '',
				about_me: '', //
				first_name: '', //
				middle_name: '',
				last_name: '',
				nation: '', //
				mobile: '', //
				email: '', //
				country: '', //
				province: '', ///
				city: '', //
				street: '', //
				zipone: 0, //
				timezone: 0, //
				skype_account: '', //skype
				video_url: '' //
			}
		}
	},
	methods: {
		initData(form) {
			this.$fillProps(this.form, form);
			this.areaLs = [+form.country, +form.province, +form.city];
			this.$nextTick(_=>{
				this.$refs.areaSelect.onInit();
			});
			this.avatar = [form.avatar];
			this.$refs.areaSelect.onInit();
		},
		onUploadSuccess() {
			this.form.avatar = e[0].download_file;
		},
		getForm() {
			this.form.avatar = this.avatar[0]
			return this.form;
		}
	}
};
</script>

<style>
.detail-container {
	border: 1px #e3e3e3 solid;
	border-radius: 3px;
}
.detail-item {
	border-bottom: 1px #e3e3e3 solid
}
.detail-item:last-of-type {
	border-bottom: none
}
.detail-item-aside {
	background-color: #eee;    
	align-items: center;
	justify-content: center;
	display: flex;
}
.detail-item-main .item-row {
	margin-bottom: 5px
}
.detail-item-main .item-row:last-of-type {
	margin-bottom: 0
}
.detail-item-main .detail-item-width1 {
	width: 100px;
}
.detail-item-main .detail-item-width2 {
	width: 205px;
}
.detail-item-main .detail-item-width3 {
	width: 310px;
}
</style>


